html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  overflow: hidden; // если используете на сайте position: sticky - уберите эту настройку
}

.is-hidden {
  display: none !important;
  /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  width: 100%;
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.section-offset {
  padding-top: 80px;
  padding-bottom: 100px;

  @media (max-width: (768px)) {
    padding-top: 50px;
    padding-bottom: 60px;
  }
}

.title {
  margin: 0;
  font-weight: 900;
  font-size: 54px;
  line-height: 140%;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: var(--brown-color);
  padding-bottom: 40px;

  @media (max-width: (768px)) {
    font-size: 34px;
    padding-bottom: 20px;
  }
  @media (max-width: (720px)) {
    font-size: 20px;
    padding-bottom: 12px;
  }
}

.title--light {
  color: var(--light-color);
}

.title--form {
  text-align: center;
  padding-bottom: 20px;

  @media (max-width: (768px)) {
    padding-bottom: 10px;
  }
  @media (max-width: (400px)) {
    margin: 0 -10px;
    font-size: 18px;
  }
}

.title--min {
  font-size: 24px;
  opacity: 0.5;

  @media (max-width: (990px)) {
    font-size: 14px;
  }
}

.btn {
  font-weight: 900;
  font-size: 18px;
  line-height: 140%;
  text-align: center;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  text-shadow: 0px 1px 2px rgba(0, 0, 0, 0.1);
  color: var(--light-color);
  background-color: var(--secondary-color);
  box-shadow: inset 0px -3px 0px rgba(0, 0, 0, 0.1);
  padding: 17px 40px;
  border-radius: 3px;

  @media (max-width: (768px)) {
    padding: 12px 35px;
    font-size: 12px;
  }
}
