.line__border,
.line__border-1,
.line__border-2,
.line__border-3,
.line__border-4 {
  position: relative;
}

.line__border-wrapper {
  position: absolute;
}

.line-secondary-main {
  content: '';
  width: 100%;
  height: 30px;
  background-color: var(--secondary-color);
}

.line-gray-main {
  content: '';
  width: 52%;
  height: 60px;
  background-color: var(--gray-color);
  transform: skew(-45deg, 0deg);
  transform-origin: top right;
}

.line-primary-main {
  content: '';
  width: 65%;
  height: 30px;
  background-color: var(--primary-color);
  transform: skew(-45deg, 0deg);
  position: absolute;
  right: -16px;
  top: 30px;
}

.line__border {
  @media (max-width: (768px)) {
    display: none;
  }
}

// =======================================

.line-gray {
  content: '';
  width: 52%;
  height: 90px;
  background-color: var(--gray-color);
  transform: skew(-45deg, 0deg);
  transform-origin: bottom;
  margin-left: auto;

  @media (max-width: (768px)) {
    height: 20px;
  }
  @media (max-width: (400px)) {
    height: 10px;
  }
}

.line-primary {
  content: '';
  width: 65%;
  height: 60px;
  background-color: var(--primary-color);
  transform: skew(-45deg, 0deg);
  position: absolute;
  right: -16px;
  top: 30px;
  position: absolute;
  left: -130px;
  top: 30px;

  @media (max-width: (768px)) {
    height: 10px;
    left: -30px;
    top: 10px;
  }
  @media (max-width: (400px)) {
    height: 5px;
    top: 5px;
  }
}

.line-secondary {
  content: "";
  width: 65%;
  height: 30px;
  background-color: var(--secondary-color);
  transform: skew(-45deg, 0deg);
  position: absolute;
  right: -90px;
  top: 30px;
  position: absolute;
  top: 60px;

  @media (max-width: (768px)) {
    height: 10px;
    right: -13px;
    top: 20px;
  }
  @media (max-width: (400px)) {
    height: 5px;
    top: 10px;
  }
}

